import * as React from "react";
import { useState } from "react";
import { sendHttpRequest, HttpVerb, ContentType } from "../../../utils/httpRequest";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import Case, { CaseItem } from "../case/Case";
import Main, { MainPages } from "../main/Main";
import Task, { TaskItem } from "../task/Task";
import Error from "../error/Error";
import Progress from "../progress/Progress";

export interface TimesheetInfo {
  minDuration: number;
}

interface TimesheetPageProps {
  cases: CaseItem[];
  selectedCase: CaseItem;
  onCaseSelected: (c: CaseItem) => void;
  tasks: TaskItem[];
  selectedTask: TaskItem;
  onTaskSelected: (t: TaskItem) => void;
  timesheetInfo: TimesheetInfo;
  getConnectionToken: () => string;
  goToPage: (page: MainPages) => void;
}

const formatTimeString = (t: string) => {
  let timeList = t.split(":");
  let hours = timeList[0] || "00";
  while (hours.length < 2) {
    hours = "0" + hours;
  }
  let minutes = timeList[1] || "00";
  while (minutes.length < 2) {
    minutes = "0" + minutes;
  }
  return hours + ":" + minutes;
};

const minutesToStimeString = (m: number) => {
  let hours = Math.floor(m / 60);
  let minutes = m - hours * 60;
  let timeString = formatTimeString(hours.toString() + ":" + minutes.toString());
  return timeString;
};

const SaveTimesheetPage: React.FC<TimesheetPageProps> = (props: TimesheetPageProps) => {
  const {
    cases,
    selectedCase,
    onCaseSelected,
    tasks,
    selectedTask,
    onTaskSelected,
    timesheetInfo,
    getConnectionToken,
    goToPage,
  } = props;
  const [error, setError] = useState<string>("");
  const [processing, setProcessing] = useState<boolean>(false);
  const [time, setTime] = useState<string>(minutesToStimeString((timesheetInfo && timesheetInfo.minDuration) || 0));
  const [nonBillable, setNonBillable] = useState<boolean>(false);
  const [description, setDescription] = useState<string>("");

  const saveTimesheet = async (
    task: TaskItem,
    c: CaseItem,
    time: string,
    nonBillable: boolean,
    description: string
  ) => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.saveTimesheet,
        ContentType.Json,
        getConnectionToken(),
        {
          task_id: task && task.id,
          case_id: c && c.id,
          time: time,
          non_billable: nonBillable,
          description: description,
        },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    }
    return response;
  };

  const onSaveTimesheet = async () => {
    setProcessing(true);
    const response = await saveTimesheet(selectedTask, selectedCase, time, nonBillable, description);
    if (response.result.success) {
      goToPage(MainPages.MainPage);
    } else {
      setError(response.result.error);
    }
    setProcessing(false);
  };

  const getTimeSection = () => {
    return (
      <div className="time-outer-container">
        <div className="page-section-title">{_t("Time")}</div>
        <div>
          <div className="form-check">
            <input className="form-check-input" type="checkbox" onChange={(e) => setNonBillable(e.target.checked)} />
            <label className="form-check-label">{_t("Non-billable")}</label>
          </div>
          <input
            className="time-input form-control mt-2"
            type="time"
            value={time}
            onChange={(e) => setTime(e.target.value)}
          />
        </div>
      </div>
    );
  };

  const getDescriptionSection = () => {
    return (
      <div className="description-outer-container">
        <div className="page-section-title">{_t("Description")}</div>
        <input
          className="name-input form-control"
          type="text"
          placeholder={_t("Describe timesheet")}
          value={description}
          onChange={(e) => setDescription(e.target.value)}
        />
      </div>
    );
  };

  const getSaveTimesheetButton = () => {
    return (
      <button title="Confirm" type="button" className="btn btn-primary" onClick={onSaveTimesheet}>
        {_t("Confirm")}
      </button>
    );
  };

  const getCancelButton = () => {
    return (
      <button title="Return" type="button" className="btn btn-default" onClick={() => goToPage(MainPages.MainPage)}>
        <i className="fa fa-arrow-left text-primary" aria-hidden="true"></i>
      </button>
    );
  };

  return (
    <div>
      <div className="page-title">
        <i className="fa fa-clock-o fa-fw text-primary"></i>&nbsp; {_t("Save Timesheet")}
      </div>
      <Error error={error} setError={setError} />
      <Case cases={cases} selectedCase={selectedCase} onCaseSelected={onCaseSelected} />
      <Task tasks={tasks} selectedTask={selectedTask} onTaskSelected={onTaskSelected} />
      {getTimeSection()}
      {getDescriptionSection()}
      <div className="d-flex justify-content-between page-button-container">
        <span>{getCancelButton()}</span>
        <span>{getSaveTimesheetButton()}</span>
      </div>
      <Progress processing={processing} />
    </div>
  );
};

export default SaveTimesheetPage;
