import * as React from "react";
import { useEffect, useState } from "react";
import { ContentType, HttpVerb, sendHttpRequest } from "../../../utils/httpRequest";
import api from "../../api";
import { CaseItem } from "../case/Case";
import { DocumentItem } from "../document/Document";
import GenerateDocumentPage from "../pages/GenerateDocumentPage";
import MainPage from "../pages/MainPage";
import OpenDocumentPage from "../pages/OpenDocumentPage";
import OpenTemplatePage from "../pages/OpenTemplatePage";
import SaveDocumentPage from "../pages/SaveDocumentPage";
import SaveTemplatePage from "../pages/SaveTemplatePage";
import SaveTimesheetPage, { TimesheetInfo } from "../pages/SaveTimesheetPage";
import { TagItem } from "../tags/tag";
import { TaskItem } from "../task/Task";
import { DocTemplateItem } from "../doc_template/DocTemplate";
import { PromptTemplateItem } from "../prompt_template/PromptTemplate";
import GenerateBasicAiTextPage from "../pages/GenerateBasicAiTextPage";
import GenerateAdvancedAiTextPage from "../pages/GenerateAdvancedAiTextPage";
import Header, { UserInfo } from "../header/Header";
import Footer from "../footer/Footer";
import Error from "../error/Error";
import { saveTranslations, translationsExpired } from "../../../utils/Translator";

/* global localStorage */

const getConnectionToken = () => {
  return "Bearer " + localStorage.getItem("odooConnectionToken");
};

export enum MainPages {
  MainPage,
  OpenDocumentPage,
  SaveDocumentPage,
  SaveTimesheetPage,
  GenerateDocumentPage,
  OpenTemplatePage,
  SaveTemplatePage,
  GenerateBasicAiTextPage,
  GenerateAdvancedAiTextPage,
}

type MainProps = {
  disconnect: () => void;
};

const Main: React.FC<MainProps> = (props: MainProps) => {
  const { disconnect } = props;
  const [error, setError] = useState<string>("");
  const [page, setPage] = useState<MainPages>(MainPages.MainPage);
  const [cases, setCases] = useState<CaseItem[]>([]);
  const [selectedCase, setSelectedCase] = useState<CaseItem>(null);
  const [documentsFromSelectedCase, setDocumentsFromSelectedCase] = useState<DocumentItem[]>([]);
  const [selectedDocument, setSelectedDocument] = useState<DocumentItem>(null);
  const [documentTags, setDocumentTags] = useState<TagItem[]>([]);
  const [tasksFromSelectedCase, setTasksFromSelectedCase] = useState<TaskItem[]>([]);
  const [selectedTask, setSelectedTask] = useState<TaskItem>(null);
  const [timesheetInfo, setTimesheetInfo] = useState<TimesheetInfo>(null);
  const [userInfo, setUserInfo] = useState<UserInfo>(null);
  const [docTemplates, setDocTemplates] = useState<DocTemplateItem[]>([]);
  const [selectedDocTemplate, setSelectedDocTemplate] = useState<DocTemplateItem>(null);
  const [promptTemplates, setpromptTemplates] = useState<PromptTemplateItem[]>([]);
  const [selectedpromptTemplate, setSelectedpromptTemplate] = useState<PromptTemplateItem>(null);

  // Helper function to get translations
  const getTranslations = async () => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getTranslations,
        ContentType.Json,
        getConnectionToken(),
        { plugin: "outlook" },
        true
      );
      response = JSON.parse(await request.promise);
      saveTranslations(response.result);
    } catch (error) {
      setError(String(error));
    }
  };

  // Helper function to get initial data loaded
  const getData = async () => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getData,
        ContentType.Json,
        getConnectionToken(),
        {},
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    }
    return response;
  };

  // Helper function to get case data
  const getCaseData = async (c: CaseItem) => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getCaseData,
        ContentType.Json,
        getConnectionToken(),
        { case_id: c && c.id },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    }
    return response;
  };

  // Set initial data when Main loads
  useEffect(() => {
    getData().then((response) => {
      if (response) {
        setCases(response.result.data.cases || []);
        setDocumentTags(response.result.data.document_tags || []);
        setDocTemplates(response.result.data.doc_templates || []);
        setTimesheetInfo(response.result.data.timesheet_info);
        setUserInfo(response.result.data.user_info);
        setpromptTemplates(response.result.data.prompt_templates || []);
      }
    });
    if (translationsExpired()) {
      getTranslations();
    }
  }, []);

  // Set the documents and tasks when case is selected
  useEffect(() => {
    selectedCase &&
      getCaseData(selectedCase).then((response) => {
        if (response) {
          if (response.result.success) {
            setDocumentsFromSelectedCase(response.result.data.documents);
            setTasksFromSelectedCase(response.result.data.tasks);
          } else {
            setError(response.result.error);
          }
        }
      });
  }, [selectedCase]);

  // Add document to case when created
  const onDocumentCreated = (d: DocumentItem) => {
    setDocumentsFromSelectedCase(documentsFromSelectedCase.concat([d]));
  };

  // Remove selected document when new case is selected
  useEffect(() => {
    if (selectedDocument) {
      if (!selectedCase) {
        setSelectedDocument(null);
      } else if (selectedCase && selectedCase.id !== selectedDocument.caseId) {
        setSelectedDocument(null);
      }
    }
  }, [selectedCase]);

  // Remove selected task when new case is selected
  useEffect(() => {
    if (selectedTask) {
      if (!selectedCase) {
        setSelectedTask(null);
      } else if (selectedCase && selectedCase.id !== selectedTask.caseId) {
        setSelectedTask(null);
      }
    }
  }, [selectedCase]);

  let mainPage = <MainPage goToPage={setPage} userInfo={userInfo} />;

  let openDocumentPage = (
    <OpenDocumentPage
      cases={cases}
      selectedCase={selectedCase}
      onCaseSelected={setSelectedCase}
      documents={documentsFromSelectedCase}
      selectedDocument={selectedDocument}
      onDocumentSelected={setSelectedDocument}
      documentTags={documentTags}
      getConnectionToken={getConnectionToken}
      goToPage={setPage}
    />
  );

  let saveDocumentPage = (
    <SaveDocumentPage
      cases={cases}
      selectedCase={selectedCase}
      onCaseSelected={setSelectedCase}
      documents={documentsFromSelectedCase}
      selectedDocument={selectedDocument}
      onDocumentSelected={setSelectedDocument}
      documentTags={documentTags}
      onDocumentCreated={onDocumentCreated}
      getConnectionToken={getConnectionToken}
      goToPage={setPage}
    />
  );

  let saveTimesheetPage = (
    <SaveTimesheetPage
      cases={cases}
      selectedCase={selectedCase}
      onCaseSelected={setSelectedCase}
      tasks={tasksFromSelectedCase}
      selectedTask={selectedTask}
      onTaskSelected={setSelectedTask}
      timesheetInfo={timesheetInfo}
      getConnectionToken={getConnectionToken}
      goToPage={setPage}
    />
  );

  let generateDocumentPage = (
    <GenerateDocumentPage
      docTemplates={docTemplates}
      selectedDocTemplate={selectedDocTemplate}
      onDocTemplateSelected={setSelectedDocTemplate}
      getConnectionToken={getConnectionToken}
      goToPage={setPage}
    />
  );

  let openTemplatePage = (
    <OpenTemplatePage
      docTemplates={docTemplates}
      selectedDocTemplate={selectedDocTemplate}
      onDocTemplateSelected={setSelectedDocTemplate}
      getConnectionToken={getConnectionToken}
      goToPage={setPage}
    />
  );

  let saveTemplatePage = (
    <SaveTemplatePage
      docTemplates={docTemplates}
      selectedDocTemplate={selectedDocTemplate}
      onDocTemplateSelected={setSelectedDocTemplate}
      getConnectionToken={getConnectionToken}
      goToPage={setPage}
    />
  );

  let generateBasicAiTextPage = <GenerateBasicAiTextPage getConnectionToken={getConnectionToken} goToPage={setPage} />;

  let generateAdvancedAiTextPage = (
    <GenerateAdvancedAiTextPage
      getConnectionToken={getConnectionToken}
      cases={cases}
      selectedCase={selectedCase}
      onCaseSelected={setSelectedCase}
      promptTemplates={promptTemplates}
      selectedPromptTemplate={selectedpromptTemplate}
      onPromptTemplateSelected={setSelectedpromptTemplate}
      goToPage={setPage}
    />
  );

  let wrapPage = (page) => (
    <>
      <Header userInfo={userInfo} />
      <div className="page">
        <Error error={error} setError={setError} />
        {page}
      </div>
      <Footer disconnect={disconnect} />
    </>
  );

  switch (page) {
    case MainPages.MainPage:
      return wrapPage(mainPage);
    case MainPages.OpenDocumentPage:
      return wrapPage(openDocumentPage);
    case MainPages.SaveDocumentPage:
      return wrapPage(saveDocumentPage);
    case MainPages.SaveTimesheetPage:
      return wrapPage(saveTimesheetPage);
    case MainPages.GenerateDocumentPage:
      return wrapPage(generateDocumentPage);
    case MainPages.OpenTemplatePage:
      return wrapPage(openTemplatePage);
    case MainPages.SaveTemplatePage:
      return wrapPage(saveTemplatePage);
    case MainPages.GenerateBasicAiTextPage:
      return wrapPage(generateBasicAiTextPage);
    case MainPages.GenerateAdvancedAiTextPage:
      return wrapPage(generateAdvancedAiTextPage);
    default:
      return mainPage;
  }
};

export default Main;
