import * as React from "react";
import { useState } from "react";
import Login from "./login/Login";
import Main from "./main/Main";

/* global localStorage */

enum Page {
  Main = "Main",
  Login = "Login",
}

const App: React.FC = () => {
  const [page, setPage] = useState<Page>(Page.Main);

  const connect = (token) => {
    localStorage.setItem("odooConnectionToken", token);
    setPage(Page.Main);
  };

  const disconnect = () => {
    localStorage.removeItem("odooConnectionToken");
    setPage(Page.Login);
  };

  const getPage = () => {
    const loginPage = <Login onLogin={connect} />;
    const mainPage = <Main disconnect={disconnect} />;

    switch (page) {
      case Page.Login:
        return loginPage;
      case Page.Main:
        return mainPage;
      default:
        return mainPage;
    }
  };

  return <div>{getPage()}</div>;
};

export default App;
